.AddMember {
  background-image: url('../../assets/logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  .add-member {
    background-color: white;
    opacity: 0.88;


    h2 {
      font-weight: 500;
      text-align: center;
      margin-bottom: 2rem;

      .application-no {
        color: darkred;
      }
    }

    .form {
      display: flex;
      column-gap: 4rem;
      max-width: 800px;
      margin: auto;

      .left,
      .right {
        flex: 50%;
        display: flex;
        flex-direction: column;
        row-gap: 1rem;
      }
    }

    .ioc-unit,
    .office-use,
    .add-family {
      margin-top: 2rem;
      margin-bottom: 1rem;
      text-align: center;
    }

    .form-family {
      max-width: 800px;
      margin: auto;

      .row {
        display: flex;
        column-gap: 2rem;
        margin-bottom: 1rem;
        align-items: center;

        .item {
          flex: 1;
        }
      }

      .action-icons {
        display: flex;
        column-gap: 2rem;
      }
    }

    .submit-btn {
      width: 400px;
      margin: auto;
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }
}